/* header.scss */

header{
    position: fixed;
    left: 0;
    top: 0;
    background: #513734;
    width: 100%;
    height: 64px;
    display: flex;
    flex-direction: column;
    color: #fff;
    padding: 0 15px;
    box-sizing: border-box;
    .status_bar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 37.5%;
        div{flex: 1;
        &:first-child{text-align: left;}
        &:nth-child(2){text-align: center;}
        &:last-child{text-align: right;}
        *{
          padding: 0 3px;
        }
        span{
            span{padding: 0;}
        }
        }
    }
    .title_bar{
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 62.5%;
    
        *{
            flex: 1;
            font-size: 16px;
        }
        h1{
            order: 2;
            text-align: center;
        }
        span{
        color:#f8ea65;
        }
        .left_item{
            order: 1;
            text-align: left;
        
            a{
            color: #fff;
            opacity: 0.5;
            }
        }
        .right_item{
            order: 3;
            text-align: right;
        
            a{
            color: #fff;
            }
            svg{
                font-size: 20px;
            }
        }
    }
}